          />
        </div>
      </td>
    </tr>

    <tr>
      <td class="text-uppercase text-sm">judul_</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <div class="sm-form">
          <input
            type="text"
            placeholder="judul_"
            id="judul"
            name="judul"
            class="form-control form-control-sm"
            v-model="vdata.judul"
          />
        </div>
      </td>
    </tr>

    <tr>
      <td class="text-uppercase text-sm">keterangan_</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <wysiwyg v-model="vdata.keterangan" />
      </td>
    </tr>

    <tr>
      <td class="text-uppercase text-sm">video_</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <div class="sm-form">
          <input
            type="text"
            placeholder="video_"
            id="video"
            name="video"
            class="form-control form-control-sm"
            v-model="vdata.video"
          />
        </div>
      </td>
    </tr>

    <tr>
      <td class="text-uppercase text-sm">link_</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <div class="sm-form">
          <input
            type="text"
            placeholder="link_"
            id="link"
            name="link"
            class="form-control form-control-sm"
            v-model="vdata.link"
          />
        </div>
      </td>
    </tr>
    <tr>
      <td class="text-uppercase text-sm">tanggal</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <div class="sm-form">
          <input
            type="date"
            placeholder="tanggal"
            id="link"
            name="link"
            class="form-control form-control-sm"
            v-model="vdata.tanggal"
          />
        </div>
      </td>
    </tr>
    <tr>
      <td class="text-uppercase text-sm">framework</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <div class="sm-form">
          <input
            type="text"
            placeholder="framework"
            id="link"
            name="link"
            class="form-control form-control-sm"
            v-model="vdata.framework"
          />
        </div>
      </td>
    </tr>
     <tr>
      <td class="text-uppercase text-sm">urutan</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <div class="sm-form">
          <input
            type="text"
            placeholder="urutan"
            id="link"
            name="link"
            class="form-control form-control-sm"
            v-model="vdata.urutan"
          />
        </div>
      </td>
    </tr>
    <tr>
      <td class="text-uppercase text-sm">TYPE</td>
      <td>&nbsp;:&nbsp;</td>
      <td style="width: 90%; padding-bottom: 5px">
        <select class="form-control" v-model="vdata.type">
          <option>WEB</option>
          <option>ANDROID/IOS</option>
          <option>IOT</option>
          <option>DESKTOP</option>
          <option>PWA</option>
          <option>AUTOMATION</option>
          <option>OTHER</option>
        </select>
      </td>
    </tr>
    <button type="button" @click="insert" class="btn btn-sm btn-primary">
      insert
    </button>
    <button type="button" @click="edit" class="btn btn-sm btn-primary">
      edit
    </button>
    <button type="button" @click="deletes" class="btn btn-sm btn-primary">
      deletes
    </button>
    <input type="file" id="filenya" name="id" @change="upload" />
    <div class="sm-form ">
      <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
    </div>
    <img :src="vdata.gambar" alt="" v-if="vdata.gambar" />
    <br>
    <hr class="style13">
    <br>
    <div class="row shadow-lg p-3 rounded-lg">
      <div class="col-6">
        Copy di text area ini >>>> <br>, paste ke ./static/datanya.json 
        <br>
        <button id="button1" class="btn btn-sm btn-danger" @click="CopyToClipboard('div1')">Click to copy</button>
        <span class="font-bold">
          lalu generate
        </span>
      </div>
      <div class="col-6" style="height:200px;overflow:scroll;" id="div1">
        {{datanya}}
        <!-- <textarea name="" id="" cols="30" rows="10" v-html="JSON.stringify(datanya)" class="border-sm border-black">
        </textarea> -->
      </div>
    </div>
    <br>
    <p class="p-3 shadow-lg rounded-lg hover:bg-blue-500 cursor-pointer " @click="ambil(item)" v-for="(item, index) in datanya" :key="index+'datanya'">
      {{item.judul}} / <span class="font-bold">{{item.type}}</span> {{item.urutan}}
    </p>
    <br>
    <br>
    <br>
    <br>
  </div>
    </div>
</template>
<script>
import axios from 'axios'
import autophp from '@/plugins/upload_autophp.js'
// import imageCompression from "browser-image-compression";
let sdb = new autophp()
import _ from 'lodash'

export default {
  data() {
    return {
      vdata: {},
      datanya: [],
    }
  },
  mounted() {
    if(prompt('secret code?')=="taufik akbar maliki"){

    }else{
      this.$router.go(-1 )
    }
    this.getData()
  },
  computed:{
    datas(){
      let data = this.datanya;
      data = _.orderBy(data, 'urutan', 'asc'); 
      console.log('datas',data)
      return data
    }
  },
  methods: {
    CopyToClipboard(containerid){
        if (document.selection) {
          var range = document.body.createTextRange();
          range.moveToElementText(document.getElementById(containerid));
          range.select().createTextRange();
          document.execCommand("copy");
        } else if (window.getSelection) {
          var range = document.createRange();
          range.selectNode(document.getElementById(containerid));
          window.getSelection().addRange(range);
          document.execCommand("copy");
          alert("Paste di static/datanya.json lalu generate")
        }
    },
    ambil(data){
      this.vdata = JSON.parse(JSON.stringify(data));
      this.$forceUpdate();
    },
    getData() {
      sdb
        .collection('portofolio')
        .doc()
        .select(`select * from portofolio`)
        .then((res) => {
          console.log(res)
          res=res.map(e=>{
            return {
              ...e,
              urutan:parseInt(e.urutan)
            }
          })
          res = _.orderBy(res, 'urutan', 'asc'); 
          this.datanya = res
          this.$forceUpdate()
        })
    },
    insert() {
      sdb
        .collection('portofolio')
        .doc()
        .set(this.vdata)
        .then((res) => {
          alert('simpan data berhasil!')
          this.getData()
        })
    },
    edit() {
      sdb
        .collection('portofolio')
        .doc()
        .set(this.vdata)
        .then((res) => {
          alert('simpan data berhasil!')
          this.getData()
        })
    },
    deletes() {
      sdb
        .collection('portofolio')
        .doc(this.vdata.id)
        .delete()
        .then((res) => {
          console.log(res)
          this.getData()
        })
    },
    async upload() {
      let that = this
      let el = document.querySelector('#filenya') // berikan id pada input file
      try {
        let file = el.files[0]
        let fd = new FormData()
        fd.append('file', file)
        fd.append('data', `select * from tbuser`) //database setting di server
        fd.append('secret', `dsdxxoi4#$(*#sdsaaada@#`) //database setting di server
        axios
          .post('https://upload.egov.balangankab.go.id/upload1.php', fd)
          .then((res) => {
            console.log(res.data)
            that.vdata.gambar =
              'https://upload.egov.balangankab.go.id' + res.data
            that.$forceUpdate()
          })
      } catch (error) {
        console.log(error)
      }
    },
  },
}
</script>